@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #333 !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.particles{
  z-index: -1;
  position: absolute;

}



.border-bottom {
	background-color: #333;
}
.home {
	align-content: center;
	justify-content: center;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 5px 1fr 0.2fr 0.5fr;
	grid-template-areas: ".... ...." "title ..." "hire-me hire-me" "social-icons social-icons";
}
.center {
	margin: auto;
	text-align: center;
}
.title {
	justify-content: center;
	width: 100vw;
	grid-area: title;
	margin: 2rem auto;
	padding: 2rem;
	text-align: center !important;
}
.title h1 {
	font-size: 3rem;
}
.title h6 {
	font-size: 1.5rem;
}

.hire-me {
	grid-area: hire-me;
	margin: auto;
}

.hire-me button {
	border: 2px solid #f2f2f2;
	padding: 1rem 2.5rem;
	border-radius: 1.3rem;
	color: white;
	background: transparent;
}

.hire-me button:hover {
	background-color: var(--green);
	color: white;
}
.social-icons {
	grid-area: social-icons;
	margin: 10px;
	font-size: 2em;
	text-align: center;
	align-content: center;
}

.social-icons a {
	text-align: center;
	margin: 5px;
	align-self: center;
	-webkit-transition: 0.5s;
	transition: 0.5s;
	text-decoration: none !important;
}
.social-icons a .fa-github {
	color: #f2f2f2;
}

.social-icons a .fa-facebook-f {
	color: #4267b2;
}
.social-icons a .fa-linkedin-in {
	color: #0077b5;
}

.social-icons a .fa-twitter {
	color: #38a1f3;
}
.social-icons a .fa-instagram {
	color: #bc2a8d;
}
.social-icons a svg:hover {
	color: var(--green);
}

.about {
	grid-template-columns: 1fr 3fr;
	grid-template-rows: auto auto 1fr;
	grid-template-areas: "about-h1 ..." "social-icons aboutme-text" ".. ..";
	padding: 1rem;
}

.about h1 {
	grid-area: about-h1;
}

.aboutme-text {
	margin: auto;
	grid-area: aboutme-text;
}

.aboutme-text a {
	color: var(--green);
}

.pict-wrapper {
	margin: 3rem auto;
	grid-area: pict-wrapper;
	height: 20vw;
	width: 30vh;
}

.pict-wrapper img {
	position: relative;
	width: 100%;
	height: 100%;
	border-radius: 50%;
	background-attachment: fixed;
	background-size: cover;
}

.tech-stack {
	justify-content: space-around;
}
.tech-stack h1 {
	padding-top: 2rem;
}
.skillsSection {
	margin: auto;
}

.tech-stack iframe {
	text-align: center;
	padding: 2rem;
	width: 90vw;
	height: 600px;
	border: 1px solid var(--dark);
	border-radius: .8rem;
	overflow: hidden;
}

.work {
	margin: auto;
	grid-gap: 1rem;
	grid-template-columns: auto auto;
	grid-template-rows: auto auto;
	grid-template-areas: "  workh1 .. .." "work-text work-text work-text" "work-0 work-1 work-2 "
		" work-3 work-4 work-5" " work-6 work-7 work-8" " work-9 work-10 work-11" " work-12 work-13 work-14"
		" work-15 work-16 work-17" " work-18 work-19 work-20" " work-21 work-22 work-23";
	padding-top: 2rem;
	padding-bottom: 1rem;
	justify-content: space-around;
}
.work h1 {
	grid-area: workh1;
}

.card a {
