.contact {
	margin: auto;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: 1rem 1fr 1fr 0.5fr;
	grid-template-areas: ".. .." "contact-text google-map " "contact-text google-map ";
	padding-top: 2em;
}
.contact-text {
	grid-area: contact-text;
	margin: 2em auto;
}

.contact-text Button {
	background-color: green;
	color: #fff;
	border-color: #ccc;
}

.google-map {
	grid-area: google-map;
	width: 100%;
	height: 500px;
}

.social {
	padding: 2em auto;
	text-align: center;
	align-content: center;
}

.social a {
	font-size: 2em;
	text-align: center;
	margin: auto 10px;
	align-content: center;
	align-self: center;
	text-decoration: none !important;
}

.social a .fa-github {
