footer {
    text-align: center;
    height: 40px;
    width: 100%;
  }
  
  footer p {
    margin: auto;
  }
  
  footer a {
    color: var(--orange);
  }
  .fa-heart{
    color: red;
  }
.App {
	flex: 1 1;
	text-align: center;
	color: #fff;
	width: 100vw;
	height: 100vh;
	overflow-x: hidden;
}
@-webkit-keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}
@keyframes App-logo-spin {
	from {
		-webkit-transform: rotate(0deg);
		        transform: rotate(0deg);
	}
	to {
		-webkit-transform: rotate(360deg);
		        transform: rotate(360deg);
	}
}

body {
	margin: 0;
}

:root {
	--light: chfff;
	/*80px is 24px from footer and 56px from navbar*/
	--stickyFooter: calc(100vh - 80px);
	--headerHeight: 200px;
}

/* Basic Setup*/

#root {
	height: 100vh;
	width: 100vw;
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
}

body {
	font-family: 'Ubuntu', sans-serif;
}

a {
	text-decoration: none;
}

a:hover,
a:link {
	color: var(--green);
	text-decoration: none;
	cursor: pointer;
}

.home,
.about,
.tech-stack,
.work,
.contact,
.not-found {
	display: grid;
	grid-column-gap: 1rem;
	min-height: calc(100vh - 80px);
	min-height: var(--stickyFooter);
	width: 90vw;
	margin: 0 auto;
}
.navbar-dark .navbar-brand,
.navbar-dark .navbar-nav .nav-link {
	color: chfff;
	color: var(--light);
}

.navbar-dark .navbar-brand:hover,
.navbar-dark .navbar-nav .nav-link:hover {
	color: var(--green);
}
.navbar-dark .navbar-nav .nav-link:focus {
	color: var(--green);
}

/* Mobile Mode */

@media (max-width: 1370px) {
	.contact {
		grid-gap: 1rem;
		grid-template-columns: 1fr;
		grid-template-rows: auto auto;
		grid-template-areas: "contact-text" "google-map";
	}
	.home {
		width: 100vw;
		grid-template-columns: 1fr 0.5fr;
		grid-template-rows: 0.05fr auto 0.5fr 0.1fr;
	}

	.title {
		width: 100vw;
		font-size: 4rem;
		grid-area: title;
		margin: auto;
		padding: 0 1rem;
	}

	.title h6 {
		padding: 1rem 0;
		margin: 0;
	}
}

@media (max-width: 992px) {
	.work {
		grid-template-columns: 1fr;
		grid-template-rows: auto auto 1fr 1fr 1fr 1fr 1fr 1fr;
		grid-template-areas: "workh1" "work-text" "work-0" "work-1" "work-2" "work-3" "work-4" "work-5" "work-6"
			"work-7" "work-8" "work-9" "work-10" "work-11" "work-12" "work-13" "work-14" "work-15" "work-16" "work-17"
			"work-18" "work-19" "work-20" "work-21" "work-22" "work-23";
	}
	.contact {
		grid-gap: 1rem;
		grid-template-columns: 1fr;
		grid-template-rows: auto auto;
		grid-template-areas: "contact-text" "google-map";
	}

	.google-map {
		height: 400px;
		padding-bottom: 1rem;
	}
	.fourZeroFour {
		font-size: 25rem;
	}
}

@media (max-width: 768px) {
	.about {
		grid-template-columns: 1fr;
		grid-template-rows: 1rem 0.1fr 0.4fr auto;
		grid-template-areas: "..." "about-h1" "social-icons" "aboutme-text";
	}
	.work {
		grid-template-columns: 1fr;
		grid-template-rows: auto auto 1fr 1fr 1fr 1fr 1fr 1fr;
		grid-template-areas: "workh1" "work-text" "work-0" "work-1" "work-2" "work-3" "work-4" "work-5" "work-6"
			"work-7" "work-8" "work-9" "work-10" "work-11" "work-12" "work-13" "work-14" "work-15" "work-16" "work-17"
			"work-18" "work-19" "work-20" "work-21" "work-22" "work-23";
	}

	.pict-wrapper {
		margin: 2rem auto;
		grid-area: pict-wrapper;
		height: 100%;
		width: 30vh;
	}
	.skillsSection a {
		text-align: center;
	}
	.skillsSection svg {
		margin: 5px;
		font-size: 9rem;
		padding: 5px;
	}
	.fourZeroFour {
		font-size: 20rem;
	}
	.social a,
	.social-icons {
		font-size: 1.5em;
		margin: auto 5px;
	}

	.title {
		font-size: 2rem;
		grid-area: title;
		margin: auto;
		padding: 0 1rem;
	}
	.title h1 {
		font-size: 2.5rem;
	}
	.title h6 {
		font-size: 1rem;
	}

	.contact-text h2 {
		font-size: 2em;
	}
}

@media (max-width: 600px) {
	.home {
		display: grid;
		grid-template-columns: 1fr;
		grid-template-rows: 1rem 1fr 0.2fr 0.5fr;
		grid-template-areas: ".... " "title" "hire-me" "social-icons";
	}

	.title {
		grid-area: title;
		margin: auto;
		padding: 0 1rem;
	}

	.fourZeroFour {
		margin: 0 auto;
		position: absolute;
		top: 50%;
		left: 50%;
		-webkit-transform: translate(-50%, -50%);
		        transform: translate(-50%, -50%);
		grid-template-columns: 1fr auto 1fr;
		grid-template-rows: 1fr 1fr 1fr;
		grid-template-areas: "... four ..." "... zero ..." "... for ...";
		font-size: 13rem;
	}
}

